export default {"name":"安溪铁观音","img":"\\wulongcha\\anxitieguanyin\\anxitieguanyin.jfif","desc":["铁观音（tiě guān yīn）茶，中国传统名茶，属于青茶类，是中国十大名茶之一。原产于福建泉州市安溪县西坪镇，发现于1723—1735年。“铁观音”既是茶名，也是茶树品种名，铁观音茶介于绿茶和红茶之间，属于半发酵茶类，铁观音独具“观音韵”，清香雅韵，冲泡后有天然的兰花香，滋味纯浓,香气馥郁持久，有“七泡有余香”之誉 。除具有一般茶叶的保健功能外，还具有抗衰老、抗动脉硬化、防治糖尿病、减肥健美、防治龋齿、清热降火，敌烟醒酒等功效。"],"videos":[{"src":"\\wulongcha\\anxitieguanyin\\cb.mp4"}],"menus":[{"name":"安溪铁观音茶叶历史","content":[{"text":"《清上明制茶法》载：“青茶（即乌龙茶）起源：福建安溪劳动人民在清雍正三年至十三年（1725-1735年）创制发明了青茶，首先传入闽北后传入台湾省。”"},{"text":"铁观音因品质优异，香味独特，各地相互仿制，先后传遍闽南、闽北、广东、台湾等乌龙茶区。"},{"src":"\\wulongcha\\anxitieguanyin\\tgy1.jpg"},{"text":"20世纪70年代，日本刮起“乌龙茶热”，乌龙茶风靡全球。江西、浙江、安徽、湖南、湖北、广西等部分绿茶区纷纷引进乌龙茶制作技术，进行“绿改乌”（即绿茶改制乌龙茶）。"},{"text":"中国乌龙茶有闽南、闽北、广东、台湾等4大产区，以福建产制历史最长，产量最多，品质最好，尤以安溪铁观音和武夷岩茶闻名于海内外。"}]},{"name":"安溪铁观音茶叶产地分布","content":[{"text":"福建安溪县既是世界名茶的故乡，也是全国名茶乌龙茶的发源地，又是福建省乌龙茶出口的基地县市。安溪产茶历史悠久，自然条件得天独厚，茶叶品质优良。"}]},{"name":"安溪铁观音茶叶品种分类","content":[{"text":"铁观音成品依发酵程度和制作工艺，大致可以分清香型、浓香型、陈香型等三大类型。"},{"text":"清香型铁观音：清香型口感比较清淡、舌尖略带微甜，偏向现代工艺制法，在市场上的占有量最多。清香型铁观音颜色翠绿，汤水清澈，香气馥郁，花香明显，口味醇正。由于新茶性寒，不可过多饮用，否则会有一定程度的伤胃、失眠。"},{"text":"浓香型铁观音：浓香型口味醇厚、香气高长、比较重回甘，是传统工艺炒制的茶叶经烘焙再加工而成产品。浓香型铁观音具有“香、浓、醇、甘”等特点，色泽乌亮，汤色金黄，香气纯正、滋味厚重，相对清香型而言，浓香型铁观音性温，有止渴生津、健脾暖胃等功效。"},{"text":"陈香型铁观音：陈香型又称老茶或熟茶，由浓香型或清香型铁观音经长时间储存，并反复再加工而成，亦属半发酵茶叶。陈香型铁观音具有“厚、醇、润、软”等特点，表现为色泽乌黑，汤水浓郁，绵甜甘醇，沉香凝韵。其特质和口味接近普洱茶及红茶、黑茶，且有沉重的历史与文化沉淀。"},{"text":"炭焙的铁观音，是铁观音浓香的一种，这也是成品乌龙茶加工的最后一道改变质量的工序，是铁观音清香型的茶叶在经过用木炭后的焙制过程。焙制的时间、次数与火候看个人口感和市场而定。"}]},{"name":"安溪铁观音茶叶加工工艺","content":[{"text":"采摘："},{"text":"3月下旬萌芽，一年分四季采制，谷雨至立夏（4月中下旬一5月上旬）为春茶，产量占全年总产量的40-45%；夏至至小暑（6月中下旬一7月上旬）为夏茶，产量占15-20%；立秋至处暑（8月上旬一8月下旬）为暑茶，产量占25-30%；秋分至寒露（9月下旬一10月上旬）为秋茶，产量占25-30%。生长地带不同的茶树鲜叶要分开，特别是早青、午青、晚青要严格分开制造，以午青品质为最优。"},{"text":"铁观音茶的采制技术特别，不是采摘非常幼嫩的芽叶， 而是采摘成熟新梢的2-3叶， 俗称“开面采”，是指叶片已全部展开，形成驻芽时采摘。"},{"text":"凉青："},{"text":"鲜叶按标准采收进厂经过凉青后进行晒青。晒青时间以午后4时阳光柔和时为宜，叶子宜薄摊，以失去原有光泽，叶色转暗，手模叶子柔软。顶叶下垂，失重6-9%左右为适度。然后移入室内凉青后进行做青。"},{"text":"做青："},{"text":"摇青与摊置相间进行，合称做青。做青技术性高，灵活性强，是决定毛茶品质优劣的关键。摇青使叶子边缘经过摩擦，叶缘细胞受损，再经过摊置，在一定的温度、湿度条件下伴随着叶子水分逐渐丧失，叶中多酚类在酶的作用下缓慢地氧化并引起了一系列化学变化，从而形成乌龙茶的特有品质。"},{"text":"铁观音鲜叶肥厚，要重摇并延长做青时间，摇青共3-5次，每次摇青的转数由少到多。摇青后摊置历时由短到长，摊叶厚度由薄到厚。第二、三次摇青必须摇到青味浓强，鲜叶硬挺，俗称“还阳”，梗叶水分重新分布平衡。第四、五次摇青，视青叶色、香变化程度而灵活掌握。做青适度的叶子，叶缘呈朱砂红色，叶中央部分呈黄绿色（半熟香蕉皮色），叶面凸起，叶缘背卷，从叶背看呈汤匙状，发出兰花香，叶张出现青蒂绿腹红边，稍有光泽，叶缘鲜红度充足，梗表皮显有皱状。"},{"text":"炒青："},{"text":"炒青要及时，当做青叶青味消失，香气初露即应抓紧进行。 揉捻、烘焙：铁观音的揉捻是多次反复进行的。初揉约3-4分钟，解块后即行初焙。焙至五、六成干，不粘手时下焙，趁热包揉，运用揉、压、搓、抓、缩等手法，经三揉三焙后，再用50-60℃的文火慢烤，使成品香气敛藏，滋味醇厚，外表色泽油亮，茶条表面凝集有-层白霜。包揉、揉捻与焙火是多次重复进行的。直到外形满意为止。最后才焙火烤干成品。"},{"text":"簸拣："},{"text":"慢烤后的茶叶最后经过簸拣，除去梗片、杂质即为成品。"}]},{"name":"安溪铁观音茶叶储藏方法","content":[{"text":"影响茶叶品质的原因有五个：水分 、温度 、空气 、光线 、异味。一般都要求低温和密封真空，这样在短的时间内可以保证铁观音的色香味，但是在实际的保存中会经常出现茶叶保存时间虽不长，但色香味却均不及开始泡饮的情况，原因在于茶叶发酵后的烘干程度。"},{"text":"1、现代贮藏方法中，效果较佳的是："},{"text":"抽气充氮贮藏法"},{"text":"是在密闭的盛器中，用氮气取代空气，使茶叶在无氧条件下贮藏。具体做法是：先将经过乾燥的茶叶装入带有卷边盖的茶罐或铝箔袋内，然后抽去空气，充入纯氮气，最后密封。茶叶贮存在充氮的盛器中，能够防止茶叶内含物耿氮化变质。在常温下，一般在一年内可保持品质不变。如能置于低温状态下贮藏效果更佳。"},{"text":"低温贮藏法"},{"text":"是在低温状况下，防止茶叶氧化变质。具体做是：先将干燥的茶叶（茶叶含水量百分之五以下），装入密封性较好的贮器中，置于摄氏三度以下低温贮藏。大宗茶叶贮存，要建造专用冷库，防潮、隔热，库内空气相对湿度保持在百分之五十左右。"},{"text":"2、居家饮用的少量茶叶，常采取："},{"text":"罐藏"},{"text":"最好是锡罐，或双层盖的铁皮罐，将茶叶用纸包好，或直接装入罐，尽量摇紧装足，不留空隙，减少罐内空气，以有利于保持茶叶的品质。装好后，盖好双层盖，盖日缝用胶纸紧封。罐外再套两层尼龙袋，并紧扎袋口，如将罐茶贮于冰箱内或干燥器内，效果更好。"},{"text":"尼龙袋贮藏等简易方法"},{"text":"尼龙袋贮藏，要选用无毒无味、密闭性较好的尼龙袋。先将茶叶用干净的白纸包好，再装入袋中，轻轻挤压，使袋内空气排出，紧扎袋口，然后再取一尼龙袋套在装好茶叶袋子外面，排气后紧扎袋口，置于乾燥、无味的铁筒内贮存。"}]},{"name":"安溪铁观音茶叶冲泡方法","content":[{"text":"品饮方式："},{"text":"使用陶制小壶、小盅（小杯），先用沸水烫热，然后在壶中装入相当於二分之一至三分之二壶容量的茶叶，冲以沸水，。头两道茶水通常会被舍弃，因茶叶初展，遛尘尚存，焦涩之味亦略重，不适合入口。这两道茶水会被用来温洗茶杯，两道水之後，茶杯也温暖待承了。第三道沸水冲入壶中，1至2分种後将茶汤匀倾入小盅内。"},{"text":"冲泡技巧："},{"text":"要从水、茶具、冲泡时间入手。水要用山泉水为佳，好的水质可以更好的发挥出茶的内质。用100℃开水冲泡最佳。"},{"text":"1、清香系列产品：原料均来自铁观音发源地安溪高海拔、岩石基质土壤种植的茶树，具有“鲜、香、韵、锐”之综合特征。"},{"text":"冲泡方法：每次5-10克放进茶杯，用沸水冲泡，首汤10-20秒即可倒出茶水，以后依次延长，但不可久浸，可连续冲泡6-7次。矿泉水或纯净水冲泡，山泉水泡饮效果最佳。"},{"text":"2、浓香系列产品：以传统工艺“茶为君，火为臣”制作的铁观音茶叶，使用百年独特的烘焙方法，温火慢烘，湿风快速冷却。"},{"text":"浓香型铁观音则要用紫砂壶泡，并且要用大嘴的。如果用小嘴的紫砂壶不利于茶叶的散热，茶叶在壶里很快就会“熟化”，茶叶的滋味会体现出“涩”的一面。"},{"text":"3、韵香系列产品特色：“观音韵”是安溪铁观音特有的韵味，是正宗铁观音的品质和特性的象征，色、香、味俱全。由于产地的原因：内安溪铁观音韵味纯正、外安溪铁观音韵味次之 、华安铁观音韵味则微。"},{"text":"4、炭焙浓香型系列产品：特色制作方法是在传统正味做法的基础上再经过120℃左右烘焙10小时左右，提高滋味醇度，发展香气。原料均来自铁观音发源地安溪高海拔、岩石基质土壤种植的茶树，经过精挑细选、传统工艺精制拼配而成。茶叶发酵充足，传统正味，具有“浓、韵、润、特”之口味，香味高，回甘好，韵味足，长期以来倍受广大消费者的青睐。韵香型铁观音要用盖碗泡，因为盖碗是白瓷制作，不吸味，导热也快。"},{"text":"注意事项："},{"text":"用保温杯泡茶的做法是错误的。茶叶如果用保温杯泡茶，长时间浸泡在高温、恒温的水中，茶叶中的茶多酚、单宁等物质会大量浸出，使茶水颜色浓重、味道苦涩。因此，茶具应选择透气性较好的陶瓷类，如用紫砂茶壶泡茶，不失茶之原味。"}]},{"name":"安溪铁观音茶叶功效作用","content":[{"text":"安溪铁观音是一种珍贵的天然饮料，具有美容保健功能。"},{"text":"美容减肥抗衰老"},{"text":"安溪铁观音的粗儿茶素组合，具较强抗氧化活性，可消除细胞中的活性氧分子，从而使人体免受衰老疾病侵害。安溪铁观音中的锰、铁、氟以及钾、钠含量比，高于其他茶叶，其中以含氟量高名列各茶类之首，对防治龋齿和老年骨骼疏松症效果显著。"},{"text":"交友养性心情好"},{"text":"安溪铁观音在待客、交友和个人修身养性方面，功效独特。安溪铁观音需要冲泡，待客时要烧水洗杯，准备过程宾主嘘寒问暖；客人边品茶边与主人叙旧，过程十分融洽亲和，故而程序化冲泡品饮，使人心静利于养性怡情。"},{"text":"防癌增智人聪明"},{"text":"安溪铁观音含硒量很高，在六大茶类中居前列。硒能刺激免疫蛋白及抗体抵御患病，抑制癌细胞发生和发展。同时安溪铁观音对增智还有功效。"},{"text":"英国科学家发现，人体大脑体液的酸碱性与智商有关。茶叶是碱性饮料，安溪铁观音碱性显著，因此常饮能调节人体酸碱平衡，提高人的智商。"},{"text":"安溪铁观音中的维生素、咖啡碱、氨基酸、矿物质、茶多酚等富含量高。"},{"text":"提神益思"},{"text":"安溪铁观音可提神益思，其功能主要在于茶叶中的咖啡碱。咖啡碱具有兴奋中枢神经、增进思维、提高效率的功能。因此，饮茶后能破睡、提神、去烦、解除疲倦、清醒头脑、增进思维，能显著地提高口头答辩能力及数学思维的反应。同时，由于安溪铁观音中含有多酚类等化合物，抵消了纯咖啡碱对人体产生的不良影响。"},{"text":"抑制心血管疾病"},{"text":"铁观音中含有的茶多酚对人体脂肪代谢有着重要作用。人体胆固醇、三酸甘油脂等含量高，血管内壁脂肪沉积，血管平滑肌细胞增生后形成动脉粥样化斑块等心血管疾病。"},{"text":"茶多酚，尤其是茶多酚中儿茶素ECG和EGC及其氧化产物茶黄素等，有助于使这种斑状增生受到抑制，使形成血凝黏度增强纤维蛋白原降低，凝血变清，从而抑制动脉粥样硬化。"},{"text":"治疗牙齿过敏"},{"text":"铁观音茶叶抗敏效果比较好。喝完的铁观音茶不要随意丢掉，可以“废物利用”，放在口中尤其是过敏的牙齿部位咀嚼一下。也可以将新鲜的铁观音茶叶直接放入牙齿的敏感部位轻轻咀嚼。用咀嚼铁观音茶叶的方法来治疗牙齿酸痛时，不必选用很高档次的铁观音茶。"}]},{"name":"安溪铁观音茶叶生活妙用","content":[{"text":"1、烫伤或烧伤 可用适量的铁观音茶叶煎取浓汁，快速冷却后，把患处浸入茶水中;也可用茶水涂抹于创面，一日4-5次。"},{"text":"2、晕车船与醉酒 事先用一小杯温茶水，加2-3毫升酱油饮下。此法也可用于解除醉酒。"},{"text":"3、刷牙时牙龈出血可经常饮茶，因茶中富含维生素C、铁质及止血成分，可使牙龈坚韧，毛细血管弹性增加，防止出血。"},{"text":"4、口臭或吸烟过度引起心慌、恶心 可用铁观音漱口并饮用适量浓茶来解除。"},{"text":"5、防治儿童龋齿 茶水中的氟可阻止牙齿在口腔酸性环境中脱磷、脱钙，故常用茶水漱口可防龋。"},{"text":"6、婴幼儿皮肤皱折处发炎红肿 可用铁观音茶叶熬水，放至适宜温度后给婴幼儿外洗。"},{"text":"7、劳累过度 泡新茶一杯饮用，能较快地消除疲劳，恢复精力。"},{"text":"8、身体肥胖者 可常饮茶水，尤其是乌龙茶，有良好的减肥作用。"},{"text":"9、胆固醇高并伴有心血管疾病者 每天饮茶水一杯，能降低胆固醇，保护心血管。"},{"text":"10、食欲不振、小便黄赤者 可多饮用些淡茶水"},{"text":"11、过食油腻不适者 可饮用较浓的热茶，如饮砖茶或沱茶，解腻效果更好。"},{"text":"12、轻微上火者可饮用铁观音来达到降火的作用。"}]},{"name":"安溪铁观音茶叶历史故事","content":[{"text":"魏说观音托梦"},{"text":"相传，1720年前后，安溪尧阳松岩村（又名松林头村）有个老茶农魏荫（1703-1775），勤于种茶，又笃信佛教，敬奉观音。每天早晚一定在观音佛前敬奉一杯清茶，几十年如一日，从未间断，有一天晚上，他睡熟了，朦胧中梦见自己扛着锄头走出家门，他来到一条溪涧旁边，在石缝中忽然发现一株茶树，枝壮叶茂，芳香诱人，跟自己所见过的茶树不同……第二天早晨，他顺着昨夜梦中的道路寻找，果然在观音仑打石坑的石隙间，找到梦中的茶树。仔细观看，只见茶叶椭圆，叶肉肥厚，嫩芽紫红，青翠欲滴：魏荫十分高兴，将这株茶树挖回种在家小一口铁鼎里，悉心培育。因这茶是观音托梦得到的，取名\"铁观音\"。"},{"text":"王说乾隆赐名"},{"text":"相传，清代乾隆元年（1736），安溪西坪尧阳南岩（今西坪镇南岩村）有位书生名叫王士让，生平喜好搜集奇花异草，曾在南山之麓筑书房，名为“南轩”。他经常与亲友聚集于南轩。"},{"text":"这年春天的一天，王士让在南轩之旁的层石中发现一株茶树，枝叶茂盛，圆叶红心，异于寻常，遂将它移植到花园中精心培育，压枝繁殖。这株茶树成茶乌润肥壮，气味超凡，冲泡后，香气馥郁，滋味甘醇。"},{"text":"乾隆六年（1741），王士让上京奉召，拜谒礼部侍郎方望溪，馈赠自己用这株茶树制成的茶。方望溪品后大加赞赏，遂献给内廷。乾隆饮后甚喜，召见王士让，垂问此茶历史。王士让如实奏享此茶的发现经过。乾隆细细观察、掂量此茶后，认为此茶乌润结实，沉重似铁，’味香形美，犹如观音，因而御赐名为“铁观音”。"},{"text":"陈年铁观音"},{"text":"在安溪民间，也流传着一个关于陈年铁观音的传说：清朝时，有个叫林福隆的员外肠胃不好，有一天肚子胀得很厉害，吃不下饭，到处找大夫看也没有用。家里人急忙到祖宅祈求神灵保佑，当天晚上，林员外做梦时得到仙人点化。第二天早上，按照梦里仙人的指点，家里人翻遍祖屋，在屋角找到色泽乌黑快要发霉的老茶，然后将其煮给林员外喝，结果，林员外肚子胀的毛病竟然好了。除了传说外，安溪民间的不少茶农每年都有存茶于陶罐的习俗，遇风寒冷热以陈茶当药。陈年铁观音不仅具有青茶的作用，还有治感冒、治消化不良、降压降脂、防治糖尿病等效用，已有专家对此进行研究证实。"},{"text":"其他传说"},{"text":"还有一些传说与以上两种传说类似。这些传说的共同点是，茶树都与观音菩萨有关，故而茶名之中有“观音”二字。而之所以冠以“铁”字，又有两种解释：一是由于茶树叶片在太阳下闪烁着“铁色”之光，另一种说法是茶经过发酵后，“茶色如铁”。"},{"text":"安溪铁观音主产区在西部的“内安溪”，这里群山环抱，峰峦绵延，云雾缭绕，土质大部分为酸性红壤，土层深厚，特别适宜茶树生长。"},{"text":"铁观音原产于福建安溪县西坪。“内安溪”为亚热带季风气候，这里的民谚说：“四季有花常见雨，严冬无雪有雷声”，可谓得天独厚，群山环抱，土质大都是红壤，呈弱酸性，非常适宜于茶的生长，又可谓得地之灵气。"},{"text":"安溪铁观音条索肥壮，卷曲紧结，汤色金黄明亮，香气馥郁，清香悠长，品之滋味浓郁，醇厚甘鲜，入口留余香，回味甘甜带蜜味，有“七泡有余香”之誉。"},{"text":"安溪铁观音“沐日月之精，收山峦之气，得烟霞之华，食之能治百病”。常饮铁观音茶，有益于祛病保健，养生长寿，犹如观音菩萨保佑一般。加之安溪铁观音茶叶冲泡之后，量重如铁，形如观音，故名\"铁观音\"。"}]},{"name":"安溪铁观音茶叶获奖情况","content":[{"text":"1982年6月，在全国名茶评比会上被评为“全国名茶”。从那以后安溪茶厂出品的特级铁观音连续20多年保持国家金质奖章的荣誉。"},{"text":"1984年，被审定为全国良种茶树。"},{"text":"1986年10月，在法国巴黎获“国际美食旅游协会金桂奖”，被评为世界十大名茶之一。"},{"text":"1995年3月，安溪县被农业部命名为“中国乌龙茶（铁观音）之乡”。"},{"text":"2001年，被农业部确定为“第一批全国无公害农产品（铁观音）生产基地县”，并被农业部、外贸部联合认定为“全国园艺产品（铁观音）出口示范区”。"},{"text":"2002年，又被农业部确认为“南亚热带作物（乌龙茶）名优基地”。"},{"text":"2004年，安溪铁观音被国家列入“原产地域保护产品”。"},{"text":"2006年1月，“安溪铁观音”证明商标被国家工商总局授予“中国驰名商标”称号，这是全国茶业第一个中国驰名商标，也是世界最喜爱的中国品牌之一。"},{"text":"2009年年10月，在上海举办的“中国世博十大名茶”上，安溪铁观音拿得第一位。"},{"text":"2010年，安溪铁观音正式进驻世博会，成为世博会茶叶第一品牌。"}]},{"name":"安溪铁观音茶叶鉴别好茶","content":[{"text":"铁观音鉴别方法是“干看外形”和“湿评内质（冲水开泡）”这两个程序。"},{"text":"1、观看外形：主要是观察铁观音的外形、色泽、匀净度和闻茶米的香气。凡外形肥状、重实、色泽砂绿，干茶（茶米）香气清纯的，此类茶即观音特征明显均为上品茶;反之为次品茶。"},{"text":"2、湿评品质：茶叶经沸水冲泡后鉴别其香气、汤色、滋味和叶底。"},{"text":"（1）嗅香气：先嗅香气是否突出，再区别香气高低、长短、强弱、纯浊。嗅香时采用热嗅、温嗅、冷嗅相结合的方法。凡香气突出，香气清高，馥郁悠长的，均为上品；反之为次品。"},{"text":"（2）尝滋味：用茶匙舀取适量的茶汤入口（一定不要太多），通过舌头在口腔中作吮吸打转滚动，使口腔各部位的味细胞作出综合的滋味感应。凡滋味醇厚，醇而带爽，厚而不涩，富有品种“韵味”特征的，均为上品;反之为次品。"},{"text":"（3）看汤色：看茶汤的颜色深浅、明暗、清浊等。凡汤色橙黄明亮的（简称是绿豆汤）的为上品;暗浊的为次品。"},{"text":"（4）观叶底：经沸水冲泡过的茶叶（称为“叶底”，俗称“茶渣”），倒入盛有清水的盘中，观察叶底。凡叶底柔软、“青蒂绿腹”明显的，均为上品;反之，为次品。"}]}]}